body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  box-sizing: border-box;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.upsert {
  margin: 0 auto;
  text-align: center;
}
.upsert label {
  text-align: left;
  display: block;
}
.upsert h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.upsert input, .upsert textarea  {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  display: block;
}


.recipe {
    background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 250px;
  margin: 16px;
  float: left;
}
.recipe h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.recipe button {
  position: relative;
  float: bottom right;
  margin-right: 10px;
  color: #f5ba13;
  border: none;
  cursor: pointer;
  outline: none;
}

.ingredients {
  background: #fff;
  border-radius: 7px;
  
  padding: 10px;
  margin: 16px;
  float: left;
  text-align: center;
}
.ingredients button {

  padding: 6px 10px;
  margin: 10px 0;
}

.ingredients input {
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  display: block;
}
.ingredients .name {
  width: 85%;
  text-align: center;
}
.ingredients .value {
  text-align: center;
  width: 15%;
}